class ConfessSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: "Hi!",
      active: false,
      text_location: 0,
      background_class: "section white"
    }
  }

  handleChangeText = () => {
    let text = ["","y l trang web tui lm ra ch  cho mnh Nhi nhn thy","Ni  tui ni nhng th m bnh thng tui khng th ni", "Bt u nha...", "C th Nhi khng nh nhng ngay t ln u gp ti trng Bnh Ph", "Khng hiu sao tui c n tng khi ln u nhn thy Nhi", "v cho n by gi, tui vn khng qun hnh nh ca Nhi ngy hm ", "Sau khi nhn tin tui cng tm thy nhng im c th chung gia tui v Nhi l...", "Yu m nhc...", "C th Nhi cng thch mu xanh...", "mu hng ?", "v cn nhng iu khc tui s tm hiu dn...", "V chc Nhi cng ang thc mc l v sao tui thch Nhi ng hong ?", "Tui thch chnh s n gin", "nh nhng...", "v c mt cht s d thng ca Nhi", "nhng chung quy li...", "tui thch mi th t Nhi...v", " thch th cn g l do", "So...", "Mnh quen nhau nha"]
    console.log(this.state.text_location + 1);
    if(!this.state.active) {
      let background = ["section blue", "section pink", "section white", "section end"];
      if(this.state.text_location + 1 === 9) {
        this.setState({
          background_class: background[0]
        })
      }else if(this.state.text_location + 1 === 10) {
        this.setState({
          background_class: background[1]
        })
      }else if(this.state.text_location + 1 === 20){
        this.setState({
          background_class: background[3]
        })
      }else {
        this.setState({
          background_class: background[2]
        })
      }

      this.setState({
        text:text[this.state.text_location + 1],
        active: true,
        text_location: this.state.text_location + 1
      })
  
      setTimeout(() => {
        this.setState({
          active: false
        })
      }, 1000)
    }
  }

  render() {
    return (
      <div className = {this.state.background_class}>
        <div className = "row container" style={{height: "100vh", display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
          <a onClick={this.handleChangeText} style={{color: "#414141"}}><h1 className={this.state.active ? "animate_title" : ""} style={this.state.text_location === 20 ? {color: "#e94256"} : this.state.background_class !== "section white" ? {color: "#fff"} :  null}>{this.state.text}</h1></a>
          <a href="https://www.facebook.com/nhatsdevil.mc" style={{color: "#414141"}}><span  style={this.state.text_location === 20 ? {color: "#e94256"} : this.state.background_class !== "section white" ? {color: "#fff"} :  null}>Welcome Ngc Nhi !</span></a>
        </div>
      </div>
    )
  }
}

