import React, {Component} from "react";
import {HashSpiner} from './Spiners';
import HashLoader from "react-spinners/HashLoader";

let quotes = [
    "Bn c bit Laven Confession c thit k v lp trnh bi Nhats Devil ?",
    "Bn c th upload nh cho confess ca mnh y!",
    "Nn nh cc ad s khng ng cc confess mang tnh cht h da, chi ln nh!",
    "Laven Confession l thnh qu ca s hp tc ca cc admin gia hai trng TCN HV v GDTX Q6"
]

class SplashScreen extends Component {
    render() {
        return(   
        <div className="splash_screen">
            <HashLoader
              size={50}
              color={"#2484FF"}
              loading={true}
            />
            <h1>{quotes[Math.floor(Math.random() * quotes.length)]}</h1>
        </div>);
    }
}

export {
    SplashScreen
}